<template>
  <div>
    <el-table
      :data="tableData"
      border
      style="margin-top: 20px">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        label="图片"
        width="200">
        <template slot-scope="scope">
          <img :src="scope.row.pic" class="img-item">
        </template>
      </el-table-column>
      <el-table-column
        label="顺序">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.priority}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="状态">
        <template slot-scope="scope">
          <el-tag type="success" v-if="scope.row.status==1">启用</el-tag>
          <el-tag type="info" v-if="scope.row.status==0">禁用</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="title"
        label="标题">
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="edit(scope.row._id)"
           > 编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="delData(scope.row._id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination @pageSizeChange="pageSizeChange"  @currentPageChange="currentPageChange"></pagination>
  </div>
</template>

<script>
  import pagination from '@/components/pagination'
  export default {
    name: "list",
    components:{
      pagination
    },
    data(){
      return{
        tableData:[],
        params:{
          pageSize: 5,
          page: 1
        }
      }
    },
    methods:{
      getdata(){
        this.$axios.post("getslideShows",this.params).then(res=>{
          this.tableData=res.data
          console.log(res.data)
        })
      },
      delData(id){
        this.$confirm('此操作将永久删除该图片, 是否继续?', '提示', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }).then(() => {
          this.$axios.post('delslideShows',{id}).then(res=>{
            if(res.code == 200){
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
              setTimeout(()=>{
                this.$router.go(0)
              },1000)
            }
          })
        }).catch((err) => {
            console.log(err)
          });
      },
      edit(id){
        this.$router.push({path:'/crmslideShows/update',query:{id}})
      },
      pageSizeChange(val){
        this.params.pageSize = val;
        this.getdata()
      },
      currentPageChange(val){
        this.params.page = val;
        this.getdata()
      }
    },
    created(){
      this.getdata()
    }
  }
</script>

<style scoped>
  .img-item {
    width: 100px;
    height:70px;
    max-height: 100px;
  }
</style>



